<template>
    <div>
        <div class="text-right mt-4" data>
            <loading :active.sync="isLoading" loader="dots"/>
            <button 
                class="btn btn-primary"
                @click="openModal(true)">新增商品</button>
        </div>
        <table class="table mt-4">
            <thead>
                <th width="120">項目</th>
                <th width="120">商品名稱</th>
                <th width="120">原價</th>
                <th width="120">售價</th>
                <th width="80">啟用狀態</th>
                <th width="80">編輯</th>
                <th width="80">刪除</th>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td>{{item.origin_price | currencyFilter}}</td>
                    <td>{{item.price | currencyFilter}}</td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>未啟用</span>
                    </td>
                    <td><button 
                            class="btn btn-outline-primary btn-sm"
                            @click="openModal(false,item)">編輯</button>
                    </td>
                    <td><button 
                            class="btn btn-outline-primary btn-sm"
                            @click="openDelProductModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>

        <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li 
                    class="page-item"
                    :class="{'disabled':pagination.has_pre === false}">
                    <a 
                        class="page-link" 
                        href="#" 
                        aria-label="Previous"
                        @click.prevent="getProducts(pagination.current_page - 1)">
                        <span aria-hidden="true">&laquo;</span>
                        <span class="sr-only">Previous</span>
                    </a>
                </li>
                <li 
                    class="page-item"
                    v-for="page in pagination.total_pages" 
                    :key="page"
                    :class="{'active': pagination.current_page === page}" >
                    <a 
                    class="page-link" 
                    href="#"
                    @click.prevent="getProducts(page)">{{page}}</a>
                </li>
                <li 
                    class="page-item"
                    :class="{'disabled':pagination. has_next=== false}">
                    <a 
                    class="page-link" 
                    href="#" 
                    aria-label="Next"
                    @click.prevent="getProducts(pagination.current_page + 1)">
                        <span aria-hidden="true">&raquo;</span>
                        <span class="sr-only">Next</span>
                    </a>
                </li>
            </ul>
        </nav>
        <!-- <Pagination :pages="pagination" @emitPages="getProducts"/> -->

        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                        <span>新增產品</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                            <label for="image">輸入圖片網址</label>
                            <input 
                                type="text" 
                                class="form-control" 
                                id="image"
                                placeholder="請輸入圖片連結"  
                                v-model="tempProduct.imageUrl"> <!-- v-model -->
                            </div>
                            <div class="form-group">
                            <label for="customFile">或 上傳圖片
                                <i 
                                class="fas fa-spinner fa-spin"
                                v-if="status.fileUploading"></i>
                            </label>
                            <input 
                                type="file" 
                                id="customFile" 
                                class="form-control"
                                ref="files"
                                @change="uploadFile"> <!--@change="uploadFile" 上傳圖片 -->
                            </div>
                            <img 
                            img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                            class="img-fluid" 
                            alt=""
                            :src="tempProduct.imageUrl"> <!---->
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                            <label for="title">標題</label>
                            <input 
                                type="text" 
                                class="form-control" 
                                id="title"
                                placeholder="請輸入標題"
                                v-model="tempProduct.title"> <!-- v-model -->
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category">項目</label>
                                <input 
                                type="text" 
                                class="form-control" 
                                id="category"
                                placeholder="請輸入項目"
                                v-model="tempProduct.category"><!-- v-model -->
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">單位</label>
                                <input 
                                type="unit" 
                                class="form-control" 
                                id="unit"
                                placeholder="請輸入單位"
                                v-model="tempProduct.unit"><!-- v-model -->
                            </div>
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                            <label for="origin_price">原價</label>
                                <input 
                                type="number" 
                                class="form-control" 
                                id="origin_price"
                                placeholder="請輸入原價"
                                v-model="tempProduct.origin_price"><!-- v-model -->
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">售價</label>
                                <input 
                                type="number" 
                                class="form-control" 
                                id="price"
                                placeholder="請輸入售價"
                                v-model="tempProduct.price"><!-- v-model -->
                            </div>
                            </div>
                            <hr>

                            <div class="form-group">
                            <label for="description">產品描述</label>
                            <textarea 
                                type="text" 
                                class="form-control" 
                                id="description"
                                placeholder="請輸入產品描述"
                                v-model="tempProduct.desciption"></textarea><!-- v-model -->
                            </div>
                            <div class="form-group">
                            <label for="content">說明內容</label>
                            <textarea 
                                type="text" 
                                class="form-control" 
                                id="content"
                                placeholder="請輸入產品說明內容"
                                v-model="tempProduct.content"></textarea><!-- v-model -->
                            </div>
                            <div class="form-group">
                            <div class="form-check">
                                <input 
                                    class="form-check-input" 
                                    type="checkbox"
                                    id="is_enabled"
                                    :true-value="1"
                                    :false-value="0"
                                    v-model="tempProduct.is_enabled"><!-- v-model -->
                                <label class="form-check-label" for="is_enabled">
                                    是否啟用
                                </label>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button 
                            type="button" 
                            class="btn btn-primary" 
                            @click="updateProduct">確認</button>     <!--  @click="updateProduct" v-on -->
                    </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button 
                    type="button" 
                    class="btn btn-danger"
                    @click="delProduct"
                    >確認刪除</button> <!--@click="delProduct"-->
                </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '../component/Pagination'

    export default{
        data(){
            return {
                products:[],
                tempProduct:{},    // for Modal data
                isNew : false,     // for Modal open decide "new" or "edit" 
                isLoading : false, // for  vue loading package  
                status:{           // for  fontawesome (局部 loading 畫面)
                    fileUploading : false,
                },
                pagination:{},     // 
            }
        },
        components:{
            Pagination
        },
        methods : {
            getProducts(page=1){
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
                const vm = this;
                this.isLoading = true;
                this.$http.get(api).then((response)=>{
                    console.log(response.data);                   
                    vm.products = response.data.products;
                    vm.isLoading = false;
                    vm.pagination = response.data.pagination;
                })
            },
            openModal(isNew,origionItem){
                if(isNew){
                    this.tempProduct = {};
                    this.isNew = true;
                }else{
                    this.tempProduct = Object.assign({},origionItem);
                    this.isNew = false;
                }
                $('#productModal').modal('show')
            },
            updateProduct(){
                let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
                let httpMethod = 'post'
                const vm = this;
                if(!vm.isNew){  // 這裡會判斷 新建產品 、 編輯產品 要使用的 api path 和 http method 是不一樣的
                    api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                    httpMethod = 'put';
                }
                this.$http[httpMethod](api,{data:vm.tempProduct}).then((response)=>{
                    console.log(response.data);
                    if(response.data.success){
                        $('#productModal').modal('hide');
                        vm.getProducts();
                    }else{
                        console.log("產品建立失敗")
                        $('#productModal').modal('hide');
                        vm.getProducts();
                    }
                })
            },
            openDelProductModal(item) {
                const vm = this;
                $('#delProductModal').modal('show');
                vm.tempProduct = Object.assign({}, item);
            },
            delProduct() {
                const vm = this;
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                this.$http.delete(url).then((response) => {
                    console.log(response, vm.tempProduct);
                    $('#delProductModal').modal('hide');
                    vm.isLoading = false;
                    this.getProducts();
                });
            },
            uploadFile(){
                console.log(this);
                const uploadedFile = this.$refs.files.files[0];
                let formData = new FormData();
                formData.append('file-to-upload',uploadedFile)
                const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
                const vm = this;
                this.status.fileUploading = true;
                this.$http.post(api,formData).then((response)=>{
                    if(response.data.success){ 
                      vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl);     // vm.tempProduct.imageUrl = response.data.imageUrl
                      vm.status.fileUploading = false;
                    }else{
                         console.log(response.data.message)
                        this.$bus.$emit('message:push',response.data.message,'danger')  // alert message
                    }
                })
            }
        },
        created(){
            this.getProducts();
        }
    }
</script>


